<template>
  <div class="shopping-cart">
    <h3>Cart</h3>

    <ShoppingCartItem v-for="item in items" :item="item" :key="item.id" />

    <ShoppingCartSummary />

    <div>
      <button class="product-btn" :disabled="orderOnLimit" @click="buy">
        Checkout
      </button>
      <p v-if="orderOnLimit">Order is over limit.</p>
    </div>
  </div>
</template>

<script>
import ShoppingCartItem from './ShoppingCartItem';
import ShoppingCartSummary from './ShoppingCartSummary';
import { mapGetters } from 'vuex';

export default {
  name: 'ShoppingCart',
  computed: mapGetters({
    items: 'cartProducts',
    orderOnLimit: 'orderOnLimit'
  }),
  components: {
    ShoppingCartItem,
    ShoppingCartSummary
  },
  methods: {
    buy() {
      window.alert("You've bought it :)");
      window.location.reload();
    }
  }
};
</script>
