<template>
  <button
    class="product-btn"
    type="button"
    :disabled="!product.inventory"
    @click="addToCart(product)"
  >
    Add to cart
  </button>
</template>

<script>
import { mapActions } from 'vuex';

export default {
  name: 'AddToCart',
  props: {
    product: Object
  },
  methods: mapActions(['addToCart'])
};
</script>
